import { EnthusiasmAction } from '../actions/enthusiasm';
import { EnthusiasmState } from '../types';
import { INCREMENT_ENTHUSIASM, DECREMENT_ENTHUSIASM } from '../constants';

// export default (state: EnthusiasmState = initialState, action: EnthusiasmAction): EnthusiasmState => {
//   switch (action.type) {
//     case INCREMENT_ENTHUSIASM:
//       return { ...state, level: state.level + 1 };
//     case DECREMENT_ENTHUSIASM:
//       return { ...state, level: Math.max(1, state.level - 1) };
//     default:
//       return state;
//   }
// };

const initialState: EnthusiasmState = {
  level: 1,
  languageName: 'TypeScript'
};

export default function(state: EnthusiasmState = initialState, action: EnthusiasmAction): EnthusiasmState {
  let partialState: Partial<EnthusiasmState> | undefined;

  switch (action.type) {
    case INCREMENT_ENTHUSIASM:
      partialState = { level: state.level + 1 }; break;
    case DECREMENT_ENTHUSIASM:
      partialState = { level: Math.max(1, state.level - 1) }; break;
    default: return state;
  }

  return { ...state, ...partialState };
};


